.controls {
  min-height: 100px;
  background-color: midnightblue;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.button {
  min-width: 120px;
  border: 0;
  padding: 4px 16px;
  margin-left: 4px;
  margin-right: 4px;
  border-radius: 3px;
  background-color: #3884ff;
  transition: all 200ms ease;
  font-family: inherit;
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  text-align: center;
  text-decoration: none;
  color: #fff;
  cursor: pointer;
}

.input {
  min-width: 240px;

  border: none;
  /* min-height: 22px; */
  margin: 0 4px;
  padding: 6px 10px;
  border-radius: 3px;
  font-family: inherit;
  font-size: 14px;
}
